import { useState } from "react";
import useDidMountEffect from "../Util/useDidMountEffect";
import "./css/HeaderButton.css";
import { useNavigate } from "react-router-dom";
import axiosApi from "../Util/api";
import getAccessToken from "../Util/checkAccessToken";

const HeaderButton = (props) => {
  const [getData, setGetData] = useState({});
  const navigate = useNavigate();

  useDidMountEffect(() => {
    setGetData({});
  }, [props.whatPage]);

  useDidMountEffect(() => {
    setGetData(props.getPageData);
  }, [props.getPageData]);

  switch (props.whatPage) {
    case "BuyPage":
      return (
        <div className="headerBtnDiv">
          {getData.isOpen ? (
            <button
              onClick={() => {
                navigate(`/orderpage/${getData.sellerId}`);
              }}
            >
               
            </button>
          ) : (
            <button disabled={true}></button>
          )}
        </div>
      );

    case "OrderPage":
      return (
        <div className="headerBtnDiv">
          <button
            onClick={() => {
              if (getData.orderName === "" && getData.orderTel === "") {
                alert("   .");
              } else if (getData.orderTel.split("-").length < 2) {
                alert(
                  "   .\n'-'   ."
                );
              } else if (getData.totalPrice === 0) {
                alert("  .");
              } else {
                if (
                  window.confirm(
                    getData.selectMenu
                      .map((menu, idx) => {
                        return `${idx + 1}. ${menu.item} x ${
                          menu.quantity
                        }\n`;
                      })
                      .join("") +
                      "\n : " +
                      getData.totalPrice +
                      "\n  - : " +
                      getData.orderName +
                      " /  : " +
                      getData.orderTel.split("-")[
                        getData.orderTel.split("-").length - 1
                      ] +
                      "\n?"
                  )
                ) {
                  axiosApi.post("/api/order", getData);
                  alert(
                    ` .   .\n: ${getData.totalPrice}`
                  );
                  navigate(`/buypage/${getData.sellerId}`);
                }
              }
            }}
          >
             
          </button>
        </div>
      );

    case "SellPage":
      return (
        <div className="headerBtnDiv">
          <button
            onClick={() => {
              const token = getAccessToken();

              const api = () => {
                axiosApi.patch(
                  `/api/shop/open`,
                  {
                    isOpen: !getData.isOpen,
                  },
                  {
                    headers: {
                      Authorization: `Bearer ${token}`,
                    },
                  }
                );
              };

              if (getData.isOpen) {
                if (window.confirm("  ?")) {
                  if (
                    window.confirm(
                      "      .\n ?"
                    )
                  ) {
                    api();
                    axiosApi.delete("/api/order/sellerId", {
                      headers: {
                        Authorization: `Bearer ${token}`,
                      },
                    });
                    window.location.reload();
                  }
                }
              } else {
                alert(" .");
                api();
                window.location.reload();
              }
            }}
          >
            {getData.isOpen ? " " : " "}
          </button>
        </div>
      );

    case "SellpageFormPage":
      const alertFunc = () => {
        if (!getData.menu) {
          alert("  .");
        } else if (!getData.location[0] || !getData.location[1]) {
          alert(
            "   .\n   /   ."
          );
        } else if (
          !getData.bankAccount[0] ||
          !getData.bankAccount[1] ||
          !getData.bankAccount[2]
        ) {
          alert(
            "    .\n /  /  ."
          );
        }
      };
      return (
        <div className="headerBtnDiv">
          <button
            onClick={() => {
              const token = getAccessToken();
              if (
                getData.menu &&
                getData.location[0] &&
                getData.location[1] &&
                getData.bankAccount[0] &&
                getData.bankAccount[1] &&
                getData.bankAccount[2]
              ) {
                axiosApi.patch(
                  "/api/shop",
                  {
                    id: getData.id,
                    location: {
                      address: getData.location[0],
                      more: getData.location[1],
                    },
                    bankAccount: {
                      bank: getData.bankAccount[0],
                      accountNum: getData.bankAccount[1],
                      name: getData.bankAccount[2],
                    },
                  },
                  {
                    headers: {
                      Authorization: `Bearer ${token}`,
                    },
                  }
                );
                navigate("/sellPage");
              } else {
                alertFunc();
              }
            }}
          >
             
          </button>
        </div>
      );

    case "UserFormPage":
      const checkEmail = () => {
        let correct_email = // eslint-disable-next-line
          /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;

        if (!correct_email.test(getData.email)) {
          return false;
        } else {
          return true;
        }
      };

      const bodyData = {
        sellerId: getData.sellerId,
        pwd: getData.pwd,
        email: getData.email,
        name: getData.name,
      };

      return (
        <div className="headerBtnDiv">
          {getData.newedit === "new" ? (
            <button
              onClick={() => {
                if (
                  !getData.sellerId ||
                  !getData.pwd ||
                  !getData.email ||
                  !getData.name
                ) {
                  alert("   .");
                } else if (!getData.idCheck) {
                  alert("   .");
                } else if (!getData.pwdCheck) {
                  alert(
                    "     ."
                  );
                } else if (!checkEmail()) {
                  alert("   .");
                } else {
                  axiosApi.post("/api/seller", bodyData);
                  axiosApi.post("/api/shop", {
                    sellerId: getData.sellerId,
                  });
                  navigate("/");
                }
              }}
            >
               
            </button>
          ) : (
            <button
              onClick={() => {
                const token = getAccessToken();

                if (!getData.sellerId || !getData.email || !getData.name) {
                  alert("   .");
                } else if (!getData.idCheck) {
                  alert("   .");
                } else if (!getData.pwdCheck) {
                  alert(
                    "     ."
                  );
                } else if (!checkEmail()) {
                  alert("   .");
                } else {
                  axiosApi
                    .patch("/api/seller", bodyData, {
                      headers: {
                        Authorization: `Bearer ${token}`,
                      },
                    })
                    .then((res) => {
                      if (res.data) {
                        localStorage.setItem("accessToken", res.data);
                      }
                    })
                    .catch((err) => {
                      console.log(err);
                    });
                  navigate("/sellPage");
                }
              }}
            >
               
            </button>
          )}
        </div>
      );

    default:
      break;
  }
};

export default HeaderButton;
